// ./components/utils/escapeCSV.js
export default function escapeCSV(value) {
  // Si es null o undefined lo dejamos vacío
  const s = value == null ? "" : String(value);

  // Forzar a que Excel lo trate como texto
  const safe = `="${s}"`;

  // Si contiene caracteres especiales, lo encerramos en comillas
  if (s.includes(",") || s.includes('"') || s.includes("\n")) {
    return '"' + safe.replace(/"/g, '""') + '"';
  }

  return safe;
}
