// ./components/Admin/EventActions.jsx
import { useState, useEffect } from "react";
import { supabase } from "../../supabaseClient";
import Button from "../ui/Button";

export default function EventActions({ eventId, onChanged, onEdit }) {
  const [allow, setAllow] = useState(true);

  // 🔹 Cargar el estado actual de inscripciones del evento
  useEffect(() => {
    const fetchEvent = async () => {
      const { data } = await supabase
        .from("events")
        .select("allow_registration")
        .eq("id", eventId)
        .single();

      if (data) setAllow(data.allow_registration);
    };

    fetchEvent();
  }, [eventId]);

  // 🔹 Activar/Desactivar inscripciones
  const toggleRegistration = async () => {
    const { error } = await supabase
      .from("events")
      .update({ allow_registration: !allow })
      .eq("id", eventId);

    if (!error) {
      setAllow(!allow);
      onChanged?.();
    } else {
      alert("Error al actualizar inscripciones: " + error.message);
    }
  };

  // 🔹 Eliminar evento
  const remove = async () => {
    if (!window.confirm("¿Eliminar evento y registros asociados?")) return;

    await supabase.from("events").delete().eq("id", eventId);
    onChanged?.();
  };

  return (
    <div
      style={{
        display: "flex",
        flexWrap: "wrap",
        gap: "8px",
        marginTop: "12px",
        paddingTop: "8px",
        borderTop: "1px solid #e5e7eb",
      }}
    >
      {/* Botón Editar */}
      {onEdit && (
        <Button
          onClick={onEdit}
          style={{ backgroundColor: "#facc15", color: "#000" }}
        >
          Editar
        </Button>
      )}

      {/* Botón Activar/Desactivar Inscripciones */}
      <Button
        onClick={toggleRegistration}
        style={{
          backgroundColor: allow ? "#6b7280" : "#16a34a",
          color: "#fff",
        }}
      >
        {allow ? "Desactivar" : "Activar "}
      </Button>

      {/* Botón Eliminar */}
      <Button
        onClick={remove}
        style={{ backgroundColor: "#ef4444", color: "#fff" }}
      >
        Eliminar
      </Button>
    </div>
  );
}
